/* fonts */
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
/* color palette */
:root {
  --layer1: #022b3a;
  --layer2: #1f7a8c;
  --layer3: #bfdbf7;
  --layer4: #e1e5f2;
  --layer5: #ffffff;
  /* material-ui */
  --primary: #1976d2;
  --secondary: #dc004e;
  /* figma */
  --figma1: #0997e3;
  --figma2: #12becc;
  --figma3: #09d976;
  --figma4: #636363;
  --figma5: #ff6584;
}

/* ========================================================= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

p {
  color: var(--figma4);
  font-size: 25px;
}

/* ============================================= */

.ingredients {
  width: 60%;
  margin: 80px auto 50px auto;
  display: flex;
  justify-content: space-between;
}

.ingredient-selector {
  height: 102px;
  width: 80px;
  border-radius: 10px;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
}

.ingredient-title {
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ingredient-title p {
  color: var(--figma1);
  font-size: 13px;
}

.ingredient-seperator {
  height: 2px;
  background-color: var(--figma2);
}

.ingredient-input {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ================================================ */

#seperator {
  height: 5px;
  background: linear-gradient(to right, var(--figma1), var(--figma3));
}

/* ================================================== */

.music {
  margin-top: 30px;
}

#create-button-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

Button {
  color: var(--figma3);
}

.play-pause {
  background-color: var(--figma3);
  border-radius: 50%;
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 10px;
}

.play-pause i {
  color: white;
}